import React from "react";
import SidebarModeChoice from "./SidebarModeChoice";

class ChooseModes extends React.Component{
	render(){
		return (
			<div className="btn-group btn-group-toggle mb-2 my-sm-auto" data-toggle="buttons">
				{
					this.props.modes.map((item) => {
						return (
							<SidebarModeChoice
								key={item.name}
								name={item.name}
								checked={this.props.selectedMode == item.code}
								clickEvent={() => {
									this.props.changeMode(item.code);
								}}
							/>
						);
					})
				}
			</div>
		);
	}
}

export default ChooseModes;