import React from "react";
import HeaderContent from "./HeaderContent";

class Header extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
			modes: [
				{ code: "raw", name: "Database" },
				{ code: "graph", name: "Grafici" },
				{ code: "map", name: "Mappa"}
			]
		};
	}

	render() {
		return (
			<>
				<HeaderContent
					big={true}
					mode={this.props.mode}
					actionShowSidebar={this.props.actionShowSidebar}
					viewSidebar={this.props.viewSidebar}
					changeMode={this.props.changeMode}
					selectedMode={this.props.selectedMode}
					modes = {this.state.modes}
					showSelector={this.props.showSelector}
				/>
				<div className="rounded red-bg mt-2 white-text control-panel p-2 justify-content-center d-md-none">
					<HeaderContent
						big={false}
						mode={this.props.mode}
						actionShowSidebar={this.props.actionShowSidebar}
						viewSidebar={this.props.viewSidebar}
						changeMode={this.props.changeMode}
						selectedMode={this.props.selectedMode}
						modes = {this.state.modes}
						showSelector={this.props.showSelector}
					/>
				</div>
			</>
		);
	}
}

export default Header;
