import React from "react";
import ChooseModes from "./ChooseModes";
class HeaderContent extends React.Component {
	render() {
		return (
			<>
				<div className={this.props.big ? "d-none" : "col-12"}>
					<h1>Dati pandemia Covid-19</h1>
					<h6 className="inline my-auto">Visualizza come: </h6>
					<ChooseModes
						changeMode={this.props.changeMode}
						selectedMode={this.props.selectedMode}
						modes={this.props.modes}
					/>
				</div>
				<div
					className={
						this.props.big
							? "rounded red-bg mt-2 white-text p-2 align-items-center justify-content-center d-none d-md-flex"
							: "col-12 align-items-center d-flex"
					}
				>
					<button className="btn btn-danger border mr-auto" onClick={this.props.actionShowSidebar}>
						{"\u00A0"}
						{this.props.viewSidebar ? "Chiudi" : "Apri"} menu{"\u00A0"}
					</button>

					<span className={this.props.big ? "" : "d-none"}>
						<h1>Dati pandemia Covid-19</h1>
						<h6 className="inline py-auto">Visualizza come: </h6>
							<ChooseModes
								changeMode={this.props.changeMode}
								selectedMode={this.props.selectedMode}
								modes={this.props.modes}
							/>
					</span>
					<button
						type="button"
						className={
							this.props.mode == "raw"
								? "btn btn-danger border ml-auto invisible"
								: "btn btn-danger border ml-auto"
						}
						onClick={this.props.showSelector}
					>
						Cambia dati
					</button>
				</div>
			</>
		);
	}
}

export default HeaderContent;
