import React from "react";
import { VectorMap } from "@south-paw/react-vector-maps";
import ItalyMap from "./italy-map.js";

class Map extends React.Component {
	constructor(props) {
		super(props);
		let posY;
		if (props.showBar) posY = -80;
		else posY = 0;
		this.state = {
			selected: "",
			mouseX: 0,
			mouseY: 0,
			dx: 0,
			posY: posY
		};
	}

	componentDidMount() {
		const delta = document.getElementById("map-div").getBoundingClientRect();
		this.setState({ dx: delta.left <= 16 ? 0 : delta.left });
		this.reloadY();
	}

	reloadY() {
		let posY;
		if (this.props.showBar) {
			posY = this.state.mouseY - 80;
		} else {
			posY = this.state.mouseY;
		}
		this.setState({ posY: posY });
	}

	render() {
		return (
			<>
				<div
					id="map-div"
					className="vheight-100"
					onMouseMove={(event) => {
						this.setState({ mouseX: event.clientX, mouseY: event.clientY });
						this.reloadY();
					}}
				>
					<VectorMap
						height={window.innerHeight * 0.7}
						{...ItalyMap}
						layerProps={{
							onMouseEnter: ({ target }) => {
								if (target.attributes.name.value != "Corsica, France")
									this.setState({ selected: target.attributes.name.value });
							},
							onMouseLeave: () => {
								this.setState({ selected: "" });
							}
						}}
					/>
				</div>
				<div
					className={this.state.selected != "" ? "border rounded red-outline d-block white-color" : "d-none"}
					style={{
						position: "absolute",
						top: this.state.posY + 25,
						//left: this.state.mouseX - document.getElementById("map-div").getBoundingClientRect().left,
						left: this.state.mouseX - this.state.dx,
						transform: `translateX(-50%)`
					}}
				>
					<p>
						<small>{this.state.selected.replace(/-/g, " ")}</small>
					</p>
					{this.state.selected != ""
						? this.props.data.map((prop) => {
								return (
									<p className="px-2 no-space" key={Object.keys(prop)[0]}>
										<small>
											{this.props.fieldsList.filter((x) => x.db == Object.keys(prop)[0])[0].name}:{" "}
											{
												prop[Object.keys(prop)[0]][
													this.state.selected.replace(/ /g, "-").replace("'", "_")
												]
											}
											{this.props.viewPercentages? " %": ""}
										</small>
									</p>
								);
						  })
						: ""}
				</div>
			</>
		);
	}
}

export default Map;
