import React from "react";

class SidebarModeChoice extends React.Component {
	render() {
		return (
			<label className={this.props.checked ? "btn btn-danger border active" : "btn btn-danger border"}>
				<input type="radio" defaultChecked={this.props.checked} onClick={this.props.clickEvent} />
				{this.props.name}
			</label>
		);
	}
}

export default SidebarModeChoice;
