import React from "react";
import "./App.css";
import Control from "./customComponents/Control.js";
import Header from "./customComponents/Header.js";
import Sidebar from "./customComponents/Sidebar.js";
import MainPage from "./customComponents/MainPage.js";
import * as Api from "./api.js";

class App extends React.Component {
	constructor(props) {
		super(props);
		const date = new Date(Date.now());
		const today = date.toISOString().substr(0, 10);
		this.state = {
			data: {},
			viewLines: [],
			viewMode: "graph",
			loading: true,
			error: false,
			showBar: false,
			graphList: ["Positivi", "Nuovi_positivi"],
			startDate: "2020-02-24",
			endDate: today,
			graphData: "STORICO",
			percentage: false,
			fieldsList: [],
			showSelector: false
		};
	}

	render() {
		return (
			<>
				<div className="App container-fluid px-xl-3 vheight-100">
					<p className={this.state.error ? "d-block" : "d-none"}>Errore con il server</p>
					<div className={this.state.error ? "d-none" : "d-block"}>
						<Header
							mode={this.state.viewMode}
							actionShowSidebar={this.showSidebar}
							viewSidebar={this.state.showBar}
							changeMode={this.changeMode}
							selectedMode={this.state.viewMode}
							showSelector={this.showSelector}
						/>
						<div className={this.state.loading ? "d-block" : "d-none"}>
							<div className="d-flex justify-content-center mt-5">
								<div className="spinner-border" role="status">
									<span className="sr-only"></span>
								</div>
							</div>
						</div>
						<div className={this.state.loading ? "d-none" : "d-block"}>
							<div className="container-fluid row ml-0 px-0 vheight-100">
								{this.state.showBar ? (
									<aside className="col-12 col-md-4 col-xl-3 d-md-block d-flex align-items-stretch mb-3 mb-sm-0">
										<Sidebar
											actionShowSidebar={this.showSidebar}
											viewLines={this.state.viewLines}
											changeState={this.alterState}
											selectedMode={this.state.viewMode}
										/>
									</aside>
								) : (
									""
								)}
								<span className={this.state.showBar ? "col-xl-9 col-md-8 px-0" : "col-12"}>
									{this.state.showSelector ? (
										<div className="border rounded red-outline my-3 pb-2">
											<Control
												currentSettings={{
													graphList: this.state.graphList,
													startDate: this.state.startDate,
													endDate: this.state.endDate,
													graphData: this.state.graphData,
													percentage: this.state.percentage
												}}
												alterSettings={this.alterDataSettings}
												alterMainState={this.alterState}
												fieldsList={this.state.fieldsList}
											/>
										</div>
									) : (
										""
									)}
									<MainPage
										viewMode={this.state.viewMode}
										data={this.state.data}
										viewLines={this.state.viewLines}
										viewPercentages={this.state.percentage}
										showBar={true}
										startDate={this.state.startDate}
										endDate={this.state.endDate}
										graphData={this.state.graphData}
										fieldsList={this.state.fieldsList}
									/>
								</span>
							</div>
						</div>
					</div>
				</div>
			</>
		);
	}

	showSidebar = () => {
		this.setState((state) => {
			return { showBar: !state.showBar };
		});
	};

	showSelector = () => {
		this.setState((state) => {
			return { showSelector: !state.showSelector };
		});
	};

	alterState = (edit) => {
		this.setState(edit);
	};

	alterDataSettings = (edit) => {
		this.setState(edit, () => {
			this.changeMode(this.state.viewMode, false);
		});
	};

	componentWillMount() {
		Api.getFieldsList().then((fields) => {
			this.setState({ fieldsList: fields.list });
		});
		this.changeMode("graph");
	}

	changeMode = (mode, changeLines = true) => {
		this.setState({ loading: true });
		switch (mode) {
			case "raw":
				Api.getRawData().then((data) => {
					this.setState({ data: data, loading: false, viewLines: [], viewMode: "raw" });
				});
				break;
			case "graph":
				Api.getGraphs(
					this.state.startDate,
					this.state.endDate,
					this.state.graphList,
					this.state.graphData,
					this.state.percentage
				).then((data) => {
					let lines;
					if (changeLines) lines = this.getViewLines(data);
					else lines = this.state.viewLines;
					this.setState({
						data: data,
						loading: false,
						viewLines: lines,
						viewMode: "graph"
					});
				});
				break;
			case "map":
				Api.getGraphs(
					this.state.startDate,
					this.state.endDate,
					this.state.graphList,
					this.state.graphData,
					this.state.percentage
				).then((data) => {
					this.setState({
						data: data,
						loading: false,
						viewLines: [],
						viewMode: "map"
					});
				});
				break;
		}
	};

	getViewLines = (data) => {
		let array = Object.keys(data[Object.keys(data)[0]][0]);
		array.splice(array.indexOf("data"), 1);
		return array.map((region) => {
			let color;
			switch (region) {
				case "Abruzzo":
					color = "#C00000";
					break;
				case "Basilicata":
					color = "#FF0000";
					break;
				case "Calabria":
					color = "#C55A11";
					break;
				case "Campania":
					color = "#FF9900";
					break;
				case "Emilia-Romagna":
					color = "#FFD966";
					break;
				case "Friuli-Venezia-Giulia":
					color = "#9FBA16";
					break;
				case "Lazio":
					color = "#548235";
					break;
				case "Liguria":
					color = "#004C00";
					break;
				case "Lombardia":
					color = "#009A46";
					break;
				case "Marche":
					color = "#0A7C66";
					break;
				case "Molise":
					color = "#08B896";
					break;
				case "Piemonte":
					color = "#00B0F0";
					break;
				case "Puglia":
					color = "#0070C0";
					break;
				case "Sardegna":
					color = "#3333CC";
					break;
				case "Sicilia":
					color = "#002060";
					break;
				case "Toscana":
					color = "#7030A0";
					break;
				case "Trentino-Alto-Adige":
					color = "#7030A0";
					break;
				case "Umbria":
					color = "#D60093";
					break;
				case "Valle-d_Aosta":
					color = "#FF00FF";
					break;
				case "Veneto":
					color = "#FF7DFF";
					break;
				default:
					color = "#000";
					break;
			}

			if (region == "Italia") {
				return {
					name: region,
					show: true,
					color: color
				};
			} else {
				return {
					name: region,
					show: false,
					color: color
				};
			}
		});
	};
}

export default App;
