import React from "react";
import SidebarItem from "./SidebarItem.js";

class Sidebar extends React.Component {
	render() {
		return (
			<div className="border red-outline red-text vheight-100 rounded mt-3 col">
				{this.props.selectedMode == "raw" ? (
					<p className="my-3">Non ci sono altre impostazioni per la modalià "Database"</p>
				) : (
					""
				)}
				{this.props.selectedMode == "map" ? (
					<p className="my-3">Non ci sono altre impostazioni per la modalià "Mappa"</p>
				) : (
					""
				)}
				{this.props.selectedMode == "graph" ? (
					<>
						<h6>Visualizza i dati di:</h6>
						<table className="mx-auto mt-2">
							<tbody>
								<SidebarItem
									item={this.props.viewLines.find((x) => x.name == "Italia")}
									viewLines={this.props.viewLines}
									changeState={this.props.changeState}
									key="Italia"
								/>
								{this.props.viewLines.map((item) => {
									if (item.name != "Italia")
										return (
											<SidebarItem
												item={item}
												viewLines={this.props.viewLines}
												changeState={this.props.changeState}
												key={item.name}
											/>
										);
								})}
							</tbody>
						</table>
						<button
							className="btn btn-small red-text"
							onClick={() => {
								this.props.changeState({
									viewLines: this.props.viewLines.map((item) => {
										return {
											name: item.name,
											show: true,
											color: item.color
										};
									})
								});
							}}
						>
							<small><b>Mostra tutti</b></small>
						</button>
					</>
				) : (
					""
				)}
			</div>
		);
	}
}

export default Sidebar;
