import React from "react";
import Graph from "./Graph.js";

//Error in the build

class Graphs extends React.Component {
	constructor(props) {
		super(props);
	}

	render() {
		return (
			<>
				{Object.keys(this.props.data).map((graph_name) => {
					return (
						<div key={graph_name} className="col-xl-6 pb-5">
							<h1>{this.props.fieldsList.filter(x => x.db == graph_name)[0].name}</h1>
							<Graph
								viewPercentages={this.props.viewPercentages}
								name={graph_name}
								data={this.props.data[graph_name]}
								viewLines={this.props.viewLines}
							/>
						</div>
					);
				})}
			</>
		);
	}
}

export default Graphs;
